/* eslint-disable react/no-danger */
import {
  useTourEventState,
  useTourStats,
} from '@containers/Home/global/TourStats';
import { useGetDetailPost } from '@containers/Home/hook/useGetDetailPost';
import { selectSceneSelected } from '@containers/Home/selectors';
import { memo, useEffect } from 'react';
import { useSelector } from 'react-redux';
import dynamic from 'next/dynamic';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import { PostSceneWrapper } from './styled';

const LoadingContainer = dynamic(() => import('@components/LoadingContainer'));

const PostScene = memo(() => {
  const sceneSelected = useSelector(selectSceneSelected);

  const [tourStats] = useTourStats('stats');
  const [tourEventState, setTourEventState] = useTourEventState('state');

  const { onToggleInfoScene, onCloseDialogPostScene } = useKrpano({});

  const { loading, detailPost } = useGetDetailPost({
    idPost: tourEventState.idPostSceneSelected,
  });

  useEffect(() => {
    if (sceneSelected?.post) onToggleInfoScene(true);
    else {
      onToggleInfoScene(false);
      setTourEventState(prev => ({ ...prev, idPostSceneSelected: '' }));
    }
    return () => {
      onToggleInfoScene(false);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sceneSelected]);

  return (
    <PostSceneWrapper
      className={`${tourEventState.visibleDialogPostScene ? 'active' : ''}`}
      isShowControlbar={tourStats.isShowControlbar}
    >
      <LoadingContainer loading={loading}>
        <div className="popInfoContentHead">
          <h4>{sceneSelected?.title}</h4>
          <i
            className="popInfoClose fal fa-times"
            onClick={onCloseDialogPostScene}
          />
        </div>
        <div className="popInfoArticle">
          <div
            className="content ctn__html"
            dangerouslySetInnerHTML={{ __html: detailPost?.content }}
          />
        </div>
      </LoadingContainer>
    </PostSceneWrapper>
  );
});

export default PostScene;
